<template>
  <breadcrumb-list :items="breadcrumbs"></breadcrumb-list>

  <div class="grid grid-cols-12 gap-4 sm:gap-5 lg:gap-6">
    <div class="col-span-12 xl:col-span-8">
      <div class="p-4 card sm:p-5">
        <form
          @input="form.clearError($event.target.name)"
          @submit.prevent="submit()"
        >
          <div class="space-y-4">
            <div>
              <label for="textName">{{ $t("attributes.name") }}</label>
              <span class="relative flex mt-1.5">
                <input
                  v-model="form.name"
                  id="textName"
                  name="name"
                  type="text"
                  class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 pl-9 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
                />
                <span
                  class="pointer-events-none absolute flex h-full w-10 items-center justify-center text-slate-400 peer-focus:text-primary dark:peer-focus:text-accent dark:text-navy-300"
                >
                  <v-icon name="swatch" size="6"></v-icon>
                </span>
              </span>

              <validation-feedback>
                {{ form.getError("name") }}
              </validation-feedback>
            </div>

            <div
              class="rounded-lg border border-slate-200 p-4 dark:border-navy-600"
            >
              <div class="space-y-4">
                <div
                  v-for="(group, index) in form.groups"
                  :key="index"
                  class="flex gap-x-2 gap-y-4"
                >
                  <div class="grow">
                    <label :for="`textGroup${index}Name`">Grup AdÄ±</label>
                    <input
                      v-model="group.name"
                      :name="`group${index}Name`"
                      :id="`textGroup${index}Name`"
                      type="text"
                      class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 placeholder:text-slate-400/70 mt-1.5 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
                    />
                  </div>
                  <div class="grow">
                    <label :for="`textGroup${index}Size`">Grup Boyutu</label>
                    <input
                      v-model="group.size"
                      :name="`group${index}Size`"
                      :id="`textGroup${index}Size`"
                      type="number"
                      min="0"
                      class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 placeholder:text-slate-400/70 mt-1.5 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
                    />
                  </div>
                  <div class="self-end">
                    <button
                      type="button"
                      class="h-9 w-9 text-slate-400 transition-colors close hover:text-slate-800"
                      :title="$t('labels.delete')"
                      @click="removeGroup(index)"
                    >
                      <i class="fa fa-lg fa-times"></i>
                    </button>
                  </div>
                </div>

                <div class="col-span-2 text-end space-x-2">
                  <v-button color="danger" :size="8" @click="removeGroup"
                    >{{ $t("labels.delete_last_group") }}
                  </v-button>
                  <v-button color="success" :size="8" @click="addGroup"
                    >{{ $t("models.group.add") }}
                  </v-button>
                </div>
              </div>
            </div>

            <div class="text-end">
              <v-button
                type="submit"
                color="primary"
                :disabled="!form.isSubmittable()"
              >
                {{ $t("labels.submit") }}
              </v-button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/core/Api";
import Form from "@/core/Form";
import Toast from "@/core/Toast";

export default {
  name: "WorkspaceForm",
  props: {
    id: {
      type: String,
      required: false,
    },
  },
  data() {
    return {
      breadcrumbs: [
        { title: this.$t("titles.workspaces"), route: "workspaces" },
        {
          title: this.id
            ? this.$t("models.workspace.edit")
            : this.$t("models.workspace.add"),
          active: true,
        },
      ],
      form: new Form({
        name: "",
        groups: [{ name: "Grup 1", size: 0 }],
      }),
    };
  },
  async created() {
    if (this.id) {
      await api.get(`/workspace/${this.id}`).then(({ data }) => {
        this.form.updateData(data);
      });
    }
  },
  methods: {
    addGroup() {
      this.form.groups.push({
        name: `${this.$t("titles.group")} ${this.form.groups.length + 1}`,
        size: 0,
      });
    },
    removeGroup(index = null) {
      if (this.form.groups.length > 1) {
        this.form.groups.splice(index ?? this.form.groups.length - 1, 1);
      }
    },
    submit() {
      if (this.form.isUpdate()) {
        this.form
          .json()
          .post("/workspace/update", this.form.getId())
          .then(() => {
            Toast.fire(this.$t("response.success.update"), "success");
          })
          .catch(() => {
            Toast.fire(this.form.getErrorMessage(), "error");
          });
      } else {
        this.form
          .json()
          .post("/workspace/store")
          .then(() => {
            Toast.fire(this.$t("response.success.store"), "success");
          })
          .catch(() => {
            Toast.fire(this.form.getErrorMessage(), "error");
          });
      }
    },
  },
};
</script>
